import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import { Router } from "@angular/router";
import { Subject } from "rxjs/Subject";

@Injectable()
export class LoginService {
    private url = 'http://localhost:4500/';
    constructor(private http: Http, private router: Router) { }
   async isAuthenticated() {
       try {
           const isAuth = await this.http.get(this.url + 'isAuthenticated').toPromise();
           return isAuth;
       } catch (error) {
           return error;
       }
    }
    postLogin(data) {
        // this.isAuthenticated$$.next(false);
        return this.http.post(this.url + 'login', data).map(res => res)
    }
    logout() {
        console.log('test');
        //get request logout needed
        this.http.get(this.url + 'logout').toPromise().then(res=>{
            window.location.href = res.url;
        })
    }
}


